import { NavLink } from 'react-router-dom'
import './style.css'

const FixedNavbar = () => {
    return (
        <div className="fixed-navbar-menu">
            <NavLink
                    to='/'
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                        <i className="icon home-outline"></i>
                        <span>Asosiy</span>
                </NavLink>
                <NavLink
                    to="/hodimlar"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                        <i className="icon people-outline"></i>
                        <span>Hodimlar</span>
                </NavLink>
                <NavLink
                    to="/maktablar"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                        <i className="icon school-outline"></i>
                        <span>Maktablar</span>
                </NavLink>
                <NavLink
                    to="/aloqa"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                        <i className="icon call-outline"></i>
                        <span>Aloqa</span>
                </NavLink>
        </div>
    )
}

export default FixedNavbar