import { useEffect, useState } from 'react';
import { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import './style.css'
import 'swiper/css';
import 'swiper/css/pagination';
import "swiper/css/navigation";
import staffData from './staffData'
import Content from './Content';

const Staff = () => {
    const [slidesPerView, setSlidesPerView] = useState(3)
    const [contentHeight, setContentHeight] = useState(null)

    const detectWidthDevice = () => {
        const width = window.innerWidth

        if (width > 1200) {
            setSlidesPerView(3)
        } else if (width > 767) {
            setSlidesPerView(2)
        } else {
            setSlidesPerView(1)
        }
    }

    const changeContentHeight = height => {
        if(!contentHeight || (contentHeight < height))
                setContentHeight(height)
    }

    useEffect(() => {
        detectWidthDevice()

        window.onresize = () => {
            detectWidthDevice()
        }
    }, [slidesPerView])

    return (
        <section id="testimonials" className="testimonials">
            <div className="container">

                <div className="section-title">
                    <h2>Hodimlar</h2>
                    <h4 style={{textAlign: 'justify'}}>
                        Туманда 2007 нафар ўқитувчилар бўлиб, шундан аёллар сони 1415 нафар (71,0%). Маълумоти бўйича таҳлил этганда 1727 нафари (86,0%) олий маълумотли, 93 нафари (5,0%) тугалланмаган олий маълумотли, 187 нафари (10,0%) ўрта махсус маълумотли ўқитувчилардир. Ўқитувчилар тоифаси. Тумандаги 1971 нафар ўқитувчилар бўлиб, шундан 95 нафари (4,8%) олий тоифали, 375 нафари (19,0%) I тоифали, 610 нафари (30,9%) II тоифали, 891 нафари (45,2%) мутахассис ўқитувчилардир.
                    </h4>
                </div>

                <div className="testimonials-slider swiper">
                    <Swiper
                        modules={[Navigation, Pagination]}
                        spaceBetween={50}
                        slidesPerView={slidesPerView}
                        navigation
                        loop={true}
                        grabCursor={true}
                        pagination={{ clickable: true }}
                        style={{ userSelect: 'none' }}
                    >
                        {
                            staffData?.map((data, index) => (
                                <SwiperSlide key={index}>
                                    <div className="testimonial-item">
                                        <Content
                                            content={data.description}
                                            changeContentHeight={changeContentHeight}
                                            contentHeight={contentHeight}
                                        />
                                        <img
                                            src={data.imgUrl}
                                            className="testimonial-img"
                                            alt="Person's Image"
                                            loading='lazy'
                                            style={{
                                                aspectRatio: 1 / 1,
                                                objectFit: 'cover',
                                                objectPosition: 'center 1%'
                                            }}
                                        />
                                        <h3>{data.fullName}</h3>
                                        <h4>{data.subTitle}</h4>
                                    </div>
                                </SwiperSlide>
                            ))
                        }
                    </Swiper>
                </div>

            </div>
        </section>
    )
}

export default Staff