import React, { useState } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

export function PieChartComputers() {
  const [data, setData] = useState(
    {
      labels: [
        '921 ta soz kompyuterlar',
        '274 ta nosoz kompyuterlar',
        '688 ta ma`nan eskirgan kompyuterlar'
      ],
      datasets: [
        {
          label: '# of Votes',
          data: [921, 274, 688],
          backgroundColor: [
            '#4F81BD',
            '#C0504D',
            '#9BBB59'
          ],
          borderColor: [
            '#4F81BD',
            '#C0504D',
            '#9BBB59',
          ],
          borderWidth: 1,
        },
      ],
    }
  )

  return <Pie data={data} />;
}