import React, { useState } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

export function PieChartSchool() {
  const [data, setData] = useState(
    {
      labels: [
        'umumta’lim maktablari',
        'iqtisoslashgan maktab',
        'davlat iqtisoslashgan maktab internati'
      ],
      datasets: [
        {
          label: '# of Votes',
          data: [46, 3, 1],
          backgroundColor: [
            '#4F81BD',
            '#C0504D',
            '#9BBB59'
          ],
          borderColor: [
            '#4F81BD',
            '#C0504D',
            '#9BBB59',
          ],
          borderWidth: 1,
        },
      ],
    }
  )

  return <Pie
    data={data}
    
  />;
}