import { useEffect, useRef, useState } from 'react'
import Loader from '../Loader'
import Pagination from '../Pagination'
import { timeFormat, viewsCountFormat } from '../util/TimeCountUtil'
import Like from './Like'
import './style.css'

const pageLimit = 8
const baseUrl = 'https://xtbapi.herokuapp.com'
// const baseUrl = 'http://localhost:3003'

const PostPanel = () => {
    const [postsData, setPostsData] = useState([])
    const [loading, setLoading] = useState(true)
    const [modalPos, setModalPos] = useState({ x: 0, y: 0 })
    const [isOpenModal, setIsOpenModal] = useState(false)
    const [modalContent, setModalContent] = useState({})
    const [pagination, setPagination] = useState(1)
    const [pageablePosts, setPageablePosts] = useState([])
    const ref = useRef(null)

    const handleClickOpenModal = (post, event) => {
        viewsCount(post._id)
        const postModalElement = ref.current
        postModalElement.style.transition = 'none'

        setModalPos({ x: event.clientX, y: event.clientY })
        setIsOpenModal(true)
        setModalContent(post)

        postModalElement.style.top = event.clientY + 'px';
        postModalElement.style.left = event.clientX + 'px';

        setTimeout(() => {
            postModalElement.classList.remove('close');
            postModalElement.style.transition = 'all .4s cubic-bezier(.08, .5, .38, 1)';
            postModalElement.classList.add('open');
            postModalElement.style.top = '50%';
            postModalElement.style.left = '50%';
        }, 5)
    }

    const handleClickCloseModal = () => {
        const postModalElement = ref.current
        const { x, y } = modalPos
        setIsOpenModal(false)

        postModalElement.classList.remove('open')
        postModalElement.classList.add('close')
        postModalElement.style.top = y + 'px';
        postModalElement.style.left = x + 'px';
    }

    const handlePagination = (pageNumber) => {
        setPagination(pageNumber)
    }

    const getAllPosts = () => {
        fetch(`${baseUrl}/posts`)
            .then(response => response.json())
            .then(data => {
                setPostsData(data)
                setTimeout(() => {
                    setLoading(false)
                }, 2000)
            })
    }

    useEffect(() => {
        getAllPosts()
    }, [])

    useEffect(() => {
        document.onkeyup = e => {
            if (isOpenModal && e.key === 'Escape') {
                handleClickCloseModal()
            }
        }
    }, [isOpenModal])

    useEffect(() => {
        const posts = []
        const dataLen = postsData?.length

        for (let i = (pagination - 1) * pageLimit; i < dataLen; i++) {
            posts.push(postsData[i])

            if (i === pagination * pageLimit - 1) break
        }

        setPageablePosts(posts)
    }, [pagination, postsData])

    // Views Count
    const viewsCount = id => {
        fetch(`${baseUrl}/posts/views/${id}`, { method: 'PUT' })
            .then(() => {
                getAllPosts()
            })
    }

    // Likes Count
    const likesCount = (id, count) => {
        fetch(`${baseUrl}/posts/likes/${id}`,
            {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ count })
            })
            .then(() => {
                getAllPosts()
            })
    }

    return (
        <div className="post-panel">
            {
                loading ?
                    <Loader /> :
                    <div className="post-container">
                        <div className="posts-wrapper">
                            {
                                pageablePosts?.map(post => (
                                    <div className="post" key={post._id}>
                                        <div className="post-image-wrapper">
                                            <div className="post-image">
                                                <img
                                                    loading="lazy"
                                                    src={`${baseUrl}${post.poster}`}
                                                    alt={post.title}
                                                />
                                                <Like likesCount={likesCount} id={post._id} />
                                                <div
                                                    className="post-image-open-btn"
                                                    onClick={event => handleClickOpenModal(post, event)}
                                                >
                                                    <i className="icon white-icon open-outline"></i>
                                                </div>
                                            </div>
                                        </div>
                                        <h4 className="title">
                                            {post.title}
                                        </h4>
                                        <div className="time-views">
                                            <div className="time">
                                                {timeFormat(new Date(post.date))}
                                            </div>
                                            <div className="views">
                                                <i className="icon black-icon heart-outline"></i>
                                                <span
                                                    className="views-count"
                                                    style={{
                                                        paddingRight: '8px'
                                                    }}
                                                >
                                                    {viewsCountFormat(post.likesCount)}
                                                </span>

                                                <i className="icon black-icon eye-outline"></i>
                                                <span className="views-count">
                                                    {viewsCountFormat(post.viewsCount)}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                        </div>

                        {
                            postsData.length > pageLimit ? (
                                <Pagination
                                    count={Math.ceil(postsData.length / pageLimit)}
                                    currentPage={pagination}
                                    handlePagination={handlePagination}
                                />
                            ) : null
                        }

                        <div
                            className='post-modal close'
                            ref={ref}
                        >
                            <div
                                className="modal-close-icon"
                                onClick={handleClickCloseModal}
                            ></div>
                            <div className="modal-container">
                                <img
                                    src={`${baseUrl}${modalContent?.poster}`}
                                    alt={modalContent?.title}
                                    loading='lazy'
                                />
                                <div className="tags-time-views">
                                    <div className="hashtags">
                                        {
                                            modalContent?.hashtags?.map((hashtag, index) => (
                                                <a href="#" key={index + hashtag}>
                                                    {hashtag}
                                                </a>
                                            ))
                                        }
                                    </div>
                                    <div className="time">
                                        {timeFormat(new Date(modalContent?.date))}
                                    </div>
                                    <div className="views">
                                        <i className="icon white-icon eye-outline"></i>
                                        <span>{modalContent?.viewsCount}</span>

                                        <i
                                            className="icon white-icon heart-outline"
                                            style={{
                                                marginLeft: '10px'
                                            }}
                                        ></i>
                                        <span>{modalContent?.likesCount}</span>
                                    </div>
                                </div>
                                <h3 className="title">
                                    {modalContent?.title}
                                </h3>
                                {
                                    modalContent?.content?.map(({ text, image }, index) => {
                                        return <div key={index}>
                                            {
                                                text?.length ? (
                                                    <p
                                                        className="text-content"
                                                    >
                                                        {text}
                                                    </p>
                                                ) : null
                                            }
                                            {
                                                image?.length ? (
                                                    <img
                                                        src={`${baseUrl}${image}`}
                                                        alt='Post Content Image'
                                                        loading='lazy'
                                                    />
                                                ) : null
                                            }
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                    </div>
            }
        </div>
    )
}

export default PostPanel