import { useState } from "react"
import Panel from "../../Components/Panel"
import SchoolsData from "./schoolsData"
import './style.css'

const Schools = () => {
    const [schoolsSerachValue, setSchoolsSearchValue] = useState('')

    const handleInputSearch = e => {
        setSchoolsSearchValue(e.target.value)
    }

    return (
        <Panel>
            <div className="section-title school-container-title">
                <h2>Maktablarga havola</h2>
            </div>

            <div className="school-container">
                <div className="search">
                    <input
                        type="search"
                        placeholder="Qidiriv..."
                        value={schoolsSerachValue}
                        onChange={handleInputSearch}
                    />
                </div>

                {
                    SchoolsData?.map(number => (
                        <a
                            href=""
                            key={`${number}-school`}
                        >
                            <div
                                className="box"
                            >
                                <i className="icon gold-icon school-outline"></i>
                                <div className="info">
                                    <h1>{number}</h1>
                                    <h2>Maktab</h2>
                                </div>
                            </div>
                        </a>
                    ))
                }
            </div>
        </Panel>
    )
}

export default Schools