import { useEffect, useRef } from "react"

const Content = ({ content, changeContentHeight, contentHeight }) => {
    const contentRef = useRef(null)

    useEffect(() => {
        changeContentHeight(contentRef.current.offsetHeight)
    }, [])

    return (
        <p
            ref={contentRef}
            style={{
                height: contentHeight + 'px'
            }}
        >
            {content}
        </p>
    )
}

export default Content