import { useState } from "react"

const Like = ({ likesCount, id }) => {
    const [active, setActive] = useState(false)

    const clickHandle =() => {
        likesCount(id, !active? 1:-1)
        setActive(!active)
    }

    return (
        <div
            className="post-image-like"
            onClick={clickHandle}
        >
            <i
                className={`icon heart${active? ' red':'-outline white'}-icon`}
            ></i>
        </div>
    )
}

export default Like