import { NavLink } from 'react-router-dom'
import './style.css'
import UzEduLogo from '../../static/UzEDU-Logo.png'

const Navbar = () => {
    return (
        <div className="navbar" id="navbar">
            <NavLink
                to="/"
                className="logo"
            >
                <img
                    src={UzEduLogo}
                    alt="Logo"
                />
            </NavLink>
            <div className="links">
                <NavLink
                    to='/'
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                    <h4>
                        <i className="icon home-outline main-color-icon"></i>
                        Asosiy
                    </h4>
                </NavLink>
                <NavLink
                    to="/hodimlar"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                    <h4>
                        <i className="icon people-outline main-color-icon"></i>
                        Hodimlar
                    </h4>
                </NavLink>
                <NavLink
                    to="/maktablar"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                    <h4>
                        <i className="icon school-outline main-color-icon"></i>
                        Maktablar
                    </h4>
                </NavLink>
                <NavLink
                    to="/aloqa"
                    className={({ isActive }) => isActive ? 'active' : null}
                >
                    <h4>
                        <i className="icon call-outline main-color-icon"></i>
                        Aloqa
                    </h4>
                </NavLink>
            </div>
            <div className="language">
                <select className="h5" name="language" id="language">
                    <option value="lotin">Lotin</option>
                    <option value="kril">Кирил</option>
                </select>
            </div>
        </div>
    )
}

export default Navbar