import BgImage from '../Images/bg-image/XTB_bg_image.JPG'
import TypeWriter from '../TypeWriter'
import './style.css'

const TopBar = () => {
    return (
        <div className="top-bar">
            <div
                className="banner"
                style={{
                    backgroundImage: `url(${BgImage})`
                }}
            >
                <TypeWriter
                    text="Mingbuloq tumani Xalq Ta'limi Bo'limi"
                />

                <div className="content">
                    <div className="item-wrapper">
                        <div className="item">
                            <i className="icon white-icon people-outline"></i>
                            <span>1 700 000</span>
                        </div>
                        <div className="item">
                            <i className="icon white-icon eye-outline"></i>
                            <span>1 700 000</span>
                        </div>
                        <div className="item">
                            <i className="icon white-icon flag-outline"></i>
                            <span>1 700 000</span>
                        </div>
                        <div className="item">
                            <i className="icon white-icon lock-closed-outline"></i>
                            <span>1 700 000</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TopBar